@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
} 
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}

.layout-boxed {
	&.section-slideshow {
		img {
			border-radius: 5px;
		}
	}
}
.layout-wide {
	&.section-slideshow {
		.slide-video {
			iframe {
				width: 100vw;
				height: calc(100vw/1.77777778);
			}
		}
	}	
}
.section-slideshow {
	.section-content-inner {
		position: relative;
	}
	.slider-container {
		position: relative;
	}
	.banner-content {
		p {
			margin: 0 0 15px;
		}
		>div:first-child {
			p {
				font-size: 26px;
				font-weight: 600;
			}
		}
		.block-html {
			margin-bottom: 15px;
		}
		button {
			display: flex;
			align-items: center;
			justify-content: center;
			min-height: 46px;
			margin-bottom: 20px;
			span {
				display: flex;
				line-height: 1.45em;
			}
			svg {
				@include margin-end(13px);
			}
			.icon-loading {
				display: none;
				width: 20px;
				height: 20px;
				path {
					stroke: none;
					fill: #fff;
				}
			}
		}
		.loading {
			.icon-cart {
				display: none;
			}
			.icon-loading {
				display: block;
			}
		}
	}
	.product-title {
		line-height: 1.2em;
	}
	.product-weight {
		margin-bottom: 30px;
		line-height: 1.2em;
	}
	.product-price {
		font-size: 36px;
		line-height: 1.12em;
		color: inherit;
		margin: 20px 0; 
		font-weight: 700;
	}
	.price-compare {
		color: inherit;
		font-weight: 400;
	}
	.price {
		color: inherit;
	}
	.block-svg_content {
	margin-bottom: 20px;
	.flex >.inline-flex {
		align-items: center;
		@include text-left;
		&:not(:last-child) {
			@include margin-end(20px);
		}
	}
	.icon {
		@include padding-end(10px);	
		svg {
			width: 20px;
			height: 20px;
		}
	}
	.text {
		font-size: 13px;
	}
} 
.container {
	.slide-video {
		iframe {
			width: var(--container-xl);
			height: calc(var(--container-xl)/1.77777778);
		}
	}
}
.tns-nav {
	bottom: 15px;
	button {
		background: var(--background-color);
		opacity: 0.67;
		&:hover {
			opacity: 1;
		}
	}
	.tns-nav-active {
		opacity: 1;
	}
}
}
@media (min-width: 1000px) {
	.section-slideshow {
		.slider-controls {
			.slider-prev-button {
				left: 20px;
			}
			.slider-next-button {
				right: 20px;
			}
		}
	}
}